.badge {
  display: flex;
  align-items: center;
  aspect-ratio: 21 / 13;
  width: min(90vw, 500px);
  border-radius: calc(min(90vw, 500px) * 0.03);
  /* background-color: var(--background-colour); */
  background-color: #ddd;
  box-shadow: 3px 3px 8px var(--shadow-color);
  rotate: -5deg;
}

.badge .cutout {
  /* position: absolute; */
  aspect-ratio: 1 / 4;
  height: 15%;
  margin-left: 3%;
  margin-right: 4%;
  background-color: var(--background-colour);
  box-shadow: inset 3px 3px 8px var(--shadow-color);
  border-radius: 100px;
  z-index: 2;
}

.badge .description {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
  color: var(--font-colour);
  z-index: 2;
}

.badge .description .company {
  font-size: calc(min(9vw, 50px) * 0.9);
  font-weight: 200;
}

.badge .description .detail {
  display: flex;
  flex-direction: column;
}

.badge .description .detail .title {
  font-size: calc(min(9vw, 50px) * 0.75);
  font-weight: bold;
}

.badge .description .detail .duration {
  font-size: calc(min(9vw, 50px) * 0.42);
  margin-top: calc(min(9vw, 50px) * -0.1);
}

.badge .duckroll {
  position: absolute;
  align-self: flex-start;
  height: 27%;
  margin-top: 4%;
  margin-left: 79%;
  z-index: 2;
}

.badge .duckroll-fill {
  fill: var(--font-colour)
}

.badge .ibm {
  margin-left: 2.5%;
  position: absolute;
  width: 95%;
  rotate: 10deg;
  z-index: 1;
  opacity: 0.2;
  /* filter: saturate(0.1) brightness(2.3); */
}